import utils, { Options } from '../utils/http';
import urls from './urls';
import Warning from '../components/Warning/index';

export default {
    // get /topics 
    topics(options = {}) {
        const opt: Options = {
            method: 'GET',
            spin: true,
            headers: {},
            mask: false,
            url: urls.topics,
            qs: options
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: object) => {
                resolve(data);
            });
        });
    },
    // get /topic/:id 
    topic(options: {id: string; accesstoken: string; mdrender: boolean}) {
        const opt: Options = {
            method: 'GET',
            spin: true,
            headers: {},
            mask: false,
            url: `${urls.topic}${options.id}`,
            qs: {
                accesstoken: options.accesstoken,
                mdrender: options.mdrender,
            }
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: object) => {
                resolve(data);
            });
        });
    },
    // post /topics 
    newTopics(options = {}) {
        const opt: Options = {
            method: 'POST',
            spin: true,
            headers: {},
            mask: false,
            url: urls.newTopics,
            qs: options,
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: any) => {
                resolve(data);
            });
        });
    },
    // post /topics/update 
    update(options = {}) {
        const opt: Options = {
            method: 'POST',
            spin: true,
            headers: {},
            mask: false,
            url: urls.update,
            qs: options,
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: any) => {
                resolve(data);
            });
        });
    },
    // post /topic_collect/collect 
    collect(options = {}) {
        const opt: Options = {
            method: 'POST',
            spin: true,
            headers: {},
            mask: false,
            url: urls.collect,
            qs: options,
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: any) => {
                resolve(data);
            });
        });
    },
    // post /topic_collect/de_collect 
    deCollect(options = {}) {
        const opt: Options = {
            method: 'POST',
            spin: true,
            headers: {},
            mask: false,
            url: urls.deCollect,
            qs: options,
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: any) => {
                resolve(data);
            });
        });
    },
    // get /topic_collect/:loginname 
    userCollect(options: {username: string}) {
        const opt: Options = {
            method: 'GET',
            spin: true,
            headers: {},
            mask: false,
            url: `${urls.userCollect}${options.username}`,
            qs: {},
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: any) => {
                resolve(data);
            });
        });
    },
    // post /topic/:topic_id/replies 
    replies(options: {topicId: string; content: string; accesstoken: string; reply_id: any}) {
        let url = `${urls.replies}${options.topicId}/replies`;
        let qs = {
            content: options.content,
            accesstoken: options.accesstoken
        } as any;
        if (options.reply_id) {
            qs.reply_id = options.reply_id;
        }
        const opt: Options = {
            method: 'POST',
            spin: true,
            headers: {},
            mask: false,
            url: `${urls.replies}${options.topicId}/replies`,
            qs,
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: any) => {
                resolve(data);
            });
        });
    },
    // post /reply/:reply_id/ups 
    ups(options: {reply_id: string; accesstoken: string}) {
        const opt: Options = {
            method: 'POST',
            spin: true,
            headers: {},
            mask: false,
            url: `${urls.ups}${options.reply_id}/ups`,
            qs: { accesstoken: options.accesstoken },
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: any) => {
                if (data.success) {
                    if (data.action === 'down') {
                        Warning.info('');
                    } else {
                        Warning.info('');
                    }
                }
                resolve(data);
            });
        });
    },
    // get /user/:loginname 
    user(options: {username: string}) {
        const opt: Options = {
            method: 'GET',
            spin: false,
            headers: {},
            mask: false,
            url: `${urls.user}${options.username}`,
            qs: {}
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: object) => {
                resolve(data);
            });
        });
    },
    // post /accesstoken  accessToken 
    accesstoken(options = {}) {
        const opt: Options = {
            method: 'POST',
            spin: true,
            headers: {},
            mask: true,
            url: urls.accesstoken,
            qs: options
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: object) => {
                resolve(data);
            });
        });
    },
    // get /message/count 
    count(options = {}) {
        const opt: Options = {
            method: 'GET',
            spin: true,
            headers: {},
            mask: true,
            url: urls.count,
            qs: options
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: object) => {
                resolve(data);
            });
        });
    },
    // get /messages 
    messages(options = {}) {
        const opt: Options = {
            method: 'GET',
            spin: true,
            headers: {},
            mask: true,
            url: urls.messages,
            qs: options
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: any) => {
                resolve(data);
            });
        });
    },
    // post /message/mark_all 
    markAll(options = {}) {
        const opt: Options = {
            method: 'POST',
            spin: true,
            headers: {},
            mask: true,
            url: urls.markAll,
            qs: options
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: any) => {
                resolve(data);
            });
        });
    },
    // post /message/mark_one/:msg_id 
    markOne(options = {}) {
        const opt: Options = {
            method: 'POST',
            spin: true,
            headers: {},
            mask: false,
            url: urls.markOne,
            qs: options
        };
        return new Promise((resolve, reject) => {
            utils.http.request(opt, (data: any) => {
                resolve(data);
            });
        });
    },
};
