import Vue from 'vue';
import Dialog from './Dialog.vue';

let DialogC = Vue.extend(Dialog);
let instance: any;

// 
let defaults = {
    title: '', // 
    showTitle: true, // 
    message: '', // body
    showInput: false, // 
    inputPlaceholder: '', // 
    inputValue: null, // 
    showInputTestError: false,
    showInputValidMoney: false,
    showClose: false,
    modalFade: false,
    lockScroll: false,
    closeOnClickModal: false,
    confirmButtonText: '', // 
    cancelButtonText: '', // 
    showOneBtn: false, // 
    oneBtnText: '', // 
    useText: false,
    text: '',
};

let initOptions = (options: any) => {
    let args = Object.assign({}, defaults, options);
    return args;
};
let getDialogInstance = (options: any) => {
    let args = initOptions(options);
    if (!instance) {
        instance = new DialogC({
            el: document.createElement('div')
        });
    }
    for (let prop in args) {
        if (args.hasOwnProperty(prop)) {
            instance[prop] = args[prop];
        }
    }
    document.body.appendChild(instance.$el);
    Vue.nextTick(() => {
        instance.visible = true;
    });
};

export default {
    open(options = {}) {
        getDialogInstance(options);
    },
    close () {
		instance.visible = false;
	}
};
